* {
    font-family: Arial, Helvetica, sans-serif;
    
}

body {
    background-color:bisque;
    padding: 60 px;
}

input {
    
    color: blue;
    font-size: 1.2 rem;
    width: 100%;
}
.card-grid {

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    }

.card{

    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;

}

.card p {

    padding: 5px;
    text-align: center;
}

.card img {

    max-height: 170px ;
}


